export default {
	props: [],
	data() {
		return {
			value: '',
			activate: false,
			textShow: false,
		};
	},
	methods: {
		sonCreate() {
			if (this.activate) {
				this.close()
			} else {
				this.open()
			}
		},
		//打开动画
		open() {
			this.activate = true
			this.$nextTick(() => {
				setTimeout(() => {
					this.textShow = true
				}, 50)
			})
		},
		//关闭动画
		close() {
			this.textShow = false
			this.$nextTick(() => {
				setTimeout(() => {
					this.activate = false
				}, 350)
			})
		},
		sonAdd() {
			if (this.value === '') {
				uni.showToast({
					title: "请输入具体内容",
					icon: "none"
				})
				return
			}
			let sonData = {
				id: 'id' + new Date().getTime(),
				content: this.value,
				checked: false,
			}
			this.$emit('sonAdd', sonData)
			this.value = ''
			this.close()
		},
	}
}
